.ourplans{
    /* border: 1px solid red; */
    overflow: hidden;
}
.ourplans > h1{
text-align: center;
padding: 10px;
margin-bottom: 20px;
color: rgb(78, 78, 78);
}
.ourplans .ourplans-cards{
    display: flex;
    justify-content: space-around;
}
.ourplans-cards .ourplans-card{
    border: 5px solid #3dd0fe;
    box-shadow: 0 0 5px rgb(145, 145, 145);
    border-radius: 10px;
    padding: 10px 20px;
    width: 21%;
}
.ourplans-card i{
    font-size: 4em;
margin-left: 100px;
margin-bottom: 20px;
}
.ourplans-card h1{
    text-align: center;
    margin-bottom: 20px;
    color: rgb(90, 90, 90);
}
.ourplans-card h2{
    text-align: center;
    margin-bottom: 20px;
    color: rgb(90, 90, 90);
}
.ourplans-card ul{
    list-style-type: none;
    color: rgb(90, 90, 90);
    font-weight: bold;
}
.ourplans-card ul li{
    margin-bottom: 10px;
}
.ourplans-card ul li::before{
    content: '\2713';
    margin-right: 5px;
    color: #3dd0fe;
}

@media (max-width:480px){
    .ourplans h1{
        font-size: 2em;
    }
    .ourplans-cards{
        flex-direction: column;
    }
    .ourplans-cards .ourplans-card{
        width: 80%;
        margin: 10px auto;
    }
    .ourplans-card h1{
    font-size: 1.8em;
    }
    .ourplans-card ul li{
        font-size: 1em;
    }
}